

.container{
    margin: 0 0 2% 2.84%;
    
}

.container .img_container{
    width: 266px;
    height: 311px;
    position: relative;
    background: rgba(247, 247, 247, 0.5);
    border: 0.5px solid #F2F2F2;
    overflow: hidden;
    margin: 0 auto;
}

.img_container > img{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(calc(-50% + 0.5px), calc(-50% + 0.5px));
}


.img_container > .fav{
    left: 92%;
    top: 21px;
    z-index: 5;
}

.container .tl{
    margin: 14px 0 9px 0;
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    color: #000000;
    width: 267px;
    height: 46px;

}

.container .price{
margin: 27px 0 12px 0;

font-family: Allan;
font-size: 22px;
font-weight: 700;
line-height: 38px;
letter-spacing: -1px;
text-align: left;

}




