.container{
width:100%;
}

.container > .register{
    height: 152px;
    background: #00002D;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0 5%;
}

.container > .register .tl{
    font-family: Arial, Helvetica, sans-serif;
    width: 358px;
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 44px;
    letter-spacing: -1px;
    text-transform: uppercase;
    color:#FFFFFF;
}

.container > .register > div{
    display: flex;
    width: 40%;
}

.container > .info{
    height: 422px;
    background: #E1DCD5;
    display: flex;
    justify-content: space-evenly;

}

.container > .info > div{
    max-width: 247px;
    display: flex;
    flex-direction: column;
    margin-top: 6%;
}


.container > .info > div .tl, a{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 22px;
    color: #333333;
}

.container > .info > div a{
    font-weight: 400;
    text-decoration: none;
}

.container > .info .tel p{
text-align: right;
}


.container > .info .socials{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 61%;
    margin: 34px 0 0 auto;
}

.container > .info .socials a {

}








.container > .register input{
    color: white;
    border-style: none;
    background: none;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 23px;
    border-bottom: 1px solid white;
    margin: 0 2%;
    outline: 0;
}

.container > .register input::placeholder{
    color: white;
}

.container button{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px 30px;
    background: #009BAA;
    border-style: none;
    justify-content: center;
    max-width: 221px;
}

.container > .info > div .tl, .distance {
    margin-bottom: 24px;
    }

.container > .info > div .distance{
    margin: 0 0 24px auto;
    }

    


.container button > p{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 11.7143px;
    line-height: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;  
}
