.container{
    width:100%;
    height: 117px;
    background: #F5F6FA;
    display:flex;
    align-items: center;
    justify-content: center;
}

 .container > div {
    display:flex;
    align-items: center;
    width:20%;
    margin: 0 1%;
 }
    
.container div > img {
    margin:0 12px;
    }

.container div > p {   
    
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 23px;
    color: #333333;
}

